footer {
   background: #7f2736;
}
.footer {
   padding: 74px 0 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   &__info {
      display: flex;
      align-items: center;
      gap: 52px;
      margin-bottom: 84px;
      @media (max-width: 480px){
         flex-direction: column;
         gap: 12px; 
         text-align: center;
         margin-bottom: 54px;
      }
   }
   &__text {
      font-weight: 600;
      font-size: 22px;
      line-height: 1.5;
      letter-spacing: 0.04em;
      color: #ffffff;
      max-width: 156px;
      width: 100%;
      @media (max-width: 480px){
         max-width: max-content;
      }
   }
   &__item {
      margin-bottom: 96px;
      @media (max-width: 480px){
         margin-bottom: 66px;
      }
   }
   &__title {
      font-weight: 500;
      font-size: 26px;
      line-height: 1.5;
      text-align: center;
      letter-spacing: 0.04em;
      color: #f0c53e;
      margin-bottom: 24px;
   }
   &__form {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      height: 60px;
      @media (max-width: 480px){
         flex-direction: column;
         gap: 10px;
         height: auto;
      }
   }
   &__form-input {
      border: 1px solid #f0c53e;
      border-radius: 80px;
      max-width: 400px;
      width: 100%;
      height: 100%;
      background: none;
      font-weight: 400;
      font-size: 20px;
      line-height: 1.5;
      letter-spacing: 0.04em;
      color: #ffffff;
      padding: 15px 30px;
      @media (max-width: 480px){
         max-width: 100%;
      }
      &::placeholder {
         color: #ffffff;
      }
      &::-moz-placeholder {
         color: #ffffff;
         opacity: 1;
      }
   }
   &__form-btn {
      height: 100%;
      padding: 14px 34px;
      background: linear-gradient(105.5deg, #ee5060 19.57%, #b25a59 78.85%);
      border-radius: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      cursor: pointer;
      @media (max-width: 480px){
         width: 100%;
         min-width: 100%;
      }
   }
   &__menu {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      max-width: 664px;
      width: 100%;
      column-gap: 40px;
      row-gap: 20px;
      @media (max-width: 480px){
         column-gap: 20px;
         row-gap: 10px;
         text-align: center;
      }
      .footer__text {
         font-weight: 400;
         color: #a7283e;
         max-width: fit-content;
         position: relative;
         transition: all .3s ease;
         &:hover{
            transition: all .3s ease;
            color: #c2324a;
         }
         &:nth-child(3), &:last-child{
            &::after{
               display: none;
            }
         }
         &::after{
            content: '';
            position: absolute;
            right: -20px;
            top: 50%;
            transform: translate(0, -50%);
            width: 1px;
            height: 17px;
            background: #9B3043;
            @media (max-width: 480px){
               display: none;
            }
         }
      }
   }
}
