.section {
   &__watch {
      padding: 0 0 256px;
   }
}
.watch {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 67px;
   @media (max-width: 992px){
      flex-direction: column;
      gap: 57px;
   }
   &__item {
      &:first-child {
         max-width: 730px;
         width: 100%;
      }
      &:nth-child(2) {
         width: 100%;
         position: relative;
         img {
            width: 100%;
            height: auto;
         }
         &::after {
            content: "";
            width: 138px;
            height: 138px;
            background: #ee5060;
            border-radius: 20px;
            position: absolute;
            left: -20px;
            top: -20px;
            z-index: -1;
         }
         &::before {
            content: "";
            width: 231px;
            height: 231px;
            background: #f0c53e;
            border-radius: 20px;
            position: absolute;
            right: -20px;
            bottom: -20px;
            z-index: -1;
         }
      }
   }
   &__title {
      font-weight: 500;
      font-size: 36px;
      line-height: 1.6;
      color: #252641;
      margin-bottom: 67px;
      position: relative;
      @media (max-width: 1110px){
         margin-bottom: 37px;
      }
      @media (max-width: 480px){
         font-size: 28px;
      }
      span {
         color: #7f2736;
      }
      &::after {
         content: "";
         width: 73px;
         height: 73px;
         background: #f0c53e;
         border-radius: 50%;
         position: absolute;
         left: -25px;
         top: -13px;
         z-index: -1;
      }
   }
   &__text {
      font-weight: 400;
      font-size: 24px;
      line-height: 1.8;
      letter-spacing: 0.02em;
      color: #2c2d32;
      position: relative;
      margin-bottom: 47px;
      @media (max-width: 1110px){
         margin-bottom: 27px;
      }
      @media (max-width: 480px){
         font-size: 18px;
      }
      &::after {
         content: "";
         width: 30px;
         height: 30px;
         background: #f0c53e;
         border-radius: 50%;
         position: absolute;
         right: 9px;
         top: 93px;
         z-index: -1;
      }
   }
   &__link {
      font-weight: 400;
      font-size: 22px;
      line-height: 1.8;
      text-decoration-line: underline;
      color: #ee5060;
   }
   &__play {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: #ffffff;
      box-shadow: 2px 20px 60px rgba(61, 155, 185, 0.1);
      width: 70px;
      height: 70px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      .play {
         width: 0;
         height: 0;
         border-radius: 1px;
         border-top: 12px solid transparent;
         border-left: 24px solid #23bdee;
         border-bottom: 12px solid transparent;
         margin-left: 10px;
      }
   }
}
