.section {
   &__news {
      padding: 0 0 142px;
   }
}
.news {
   &__title {
      font-family: "Nunito Sans";
      font-weight: 700;
      font-size: 36px;
      line-height: 1.8;
      color: #7f2736;
      text-align: center;
      margin-bottom: 20px;
      @media (max-width: 480px){
         font-size: 30px;
      }
   }
   &__text {
      font-family: "Nunito Sans";   
      font-weight: 400;
      font-size: 24px;
      line-height: 180%;
      text-align: center;
      color: #2c2d32;
      margin-bottom: 100px;
      @media (max-width: 480px){
         font-size: 20px;
         margin-bottom: 50px;
      }
   }
   &__items {
      display: flex;
      justify-content: center;
      gap: 50px;
      @media (max-width: 1332px){
         flex-direction: column;
      }
   }
   &__item {
      &:nth-child(2){
         .news__item-img{
            position: relative;
            min-width: 280px;
            width: 100%;
            height: 100%;
            margin-bottom: 0;
            img{
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
            .news__label{
               position: absolute;
               bottom: 20px;
               right: 20px;
               margin-bottom: 0;
            }
         }
      }
   }
   &__item-img {
      width: 100%;
      margin-bottom: 40px;
      @media (max-width: 480px){
         margin-bottom: 20px;
      }
      img {
         width: 100%;
      }
   }
   &__label {
      padding: 2px 36px;
      background: #f0c53e;
      border-radius: 80px;
      margin-bottom: 20px;
      max-width: fit-content;
   }
   &__item-title {
      font-weight: 500;
      font-size: 26px;
      line-height: 1.8;
      color: #7f2736;
      margin-bottom: 37px;
      @media (max-width: 480px){
         margin-bottom: 17px;
         font-size: 22px;
      }
   }
   &__item-text {
      font-weight: 400;
      font-size: 20px;
      line-height: 1.8;
      letter-spacing: 0.02em;
      color: #2c2d32;
      font-size: 16px;
   }
   &__item-link {
      text-decoration-line: underline;
      color: #ee5060;
      margin-top: 22px;
   }
   &__content {
      display: flex;
      gap: 20px;
      margin-bottom: 50px;
      @media (max-width: 768px){
         flex-direction: column;
      }
   }
   &__block {
      .news__item-title{
         margin-bottom: 22px;
      }
   }
}
