header {
   background: #f0c53e;
   border-radius: 0px 0px 100% 100% / 0px 0px 35% 35%;
   @media (max-width: 1110px) {
      border-radius: 0px 0px 100% 100% / 0px 0px 15% 15%;
   }
   @media (max-width: 480px) {
      border-radius: 0px 0px 100% 100% / 0px 0px 5% 5%;
   }
}
nav {
   &.fixed__nav {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 9999;
      background: #f0c53e;
      .nav {
         padding: 20px 0;
         transition: all 0.3s ease;
      }
   }
}
.nav {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 60px 0;
   transition: all 0.3s ease;
   position: relative;
   @media (max-width: 992px) {
      padding: 30px 0;
   }
   &__container {
      padding: 0 105px;
      @media (min-width: 1920px) {
         max-width: 1720px;
         margin: 0 auto;
         padding: 0;
      }
      @media (min-width: 1440px), (max-width: 1332px) {
         padding: 0 30px;
      }
      @media (max-width: 480px) {
         padding: 0 10px;
      }
   }
   &__logo {
      @media (max-width: 1332px) {
         width: 160px;
         img {
            width: 100%;
            height: auto;
         }
      }
      @media (max-width: 480px) {
         width: 120px;
      }
   }
   &__menu {
      @media (max-width: 992px) {
         display: none;
         &.active {
            display: flex;
            justify-content: center;
            align-items: center;
            position: fixed;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100vh;
            background: #f0c53e;
            overflow-x: hidden !important;
            z-index: 99;
            animation: fade-in 0.2s linear;
         }
      }
   }
   &__list {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;
      @media (min-width: 1440px) {
         gap: 40px;
      }
      @media (min-width: 1920px) {
         gap: 97px;
      }
      @media (max-width: 992px) {
         flex-direction: column;
      }
   }
   &__list-link {
      font-family: "Poppins";
      font-weight: 400;
      font-size: 22px;
      line-height: 1.5;
      letter-spacing: 0.02em;
      color: #252641;
      position: relative;
      &::after {
         content: "";
         position: absolute;
         left: 0;
         right: 0;
         bottom: 0;
         width: 100%;
         height: 2px;
         background: #252641;
         transform: scaleX(0);
         transition: all 0.3s ease;
      }
      &:hover::after {
         transform: scaleX(1);
         transition: all 0.3s ease;
      }
   }
   &__list-btn {
      color: #ffffff;
      font-weight: 500;
      display: inline-flex;
      align-items: center;
      gap: 21px;
      padding: 23px 39px;
      background: #b25a59;
      border-radius: 80px;
      margin-left: 35px;
      transition: all 0.3s ease;
      &::after {
         display: none;
      }
      img {
         transition: all 0.3s ease;
      }
      @media (max-width: 1332px) {
         gap: 10px;
         padding: 15px 30px;
         margin-left: 20px;
         img {
            width: 20px;
         }
      }
      @media (max-width: 992px) {
         margin-left: 0;
      }
      &:hover {
         transition: all 0.3s ease;
         background: #964b49;
         img {
            transform: rotate(-20deg);
            transition: all 0.3s ease;
         }
      }
   }
   .burger {
      @media (max-width: 992px) {
         width: 30px;
         height: 20px;
         z-index: 1001;
         position: absolute;
         right: 0;
         &::after,
         &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            border-radius: 2px;
            left: 0;
            background-color: #252641;
            transition: all 0.3s ease;
         }
         &.active-burger::before {
            transform: rotate(45deg);
            top: 8.5px;
         }
         &.active-burger::after {
            transform: rotate(-45deg);
            bottom: 8px;
         }
         &::before {
            top: 0;
         }
         &::after {
            bottom: 0;
         }
         span {
            position: absolute;
            background-color: #252641;
            right: 0;
            width: 100%;
            height: 3px;
            top: 50%;
            transform: translate(0, -50%);
            transition: all 0.3s ease;
            border-radius: 2px;
         }
         &.active-burger span {
            transform: scale(0);
         }
      }
   }
}
.section {
   &__hero {
      padding: 132px 0 149px;
      @media (max-width: 1110px) {
         padding: 50px 0 149px;
      }
   }
}
.hero {
   display: flex;
   align-items: center;
   gap: 106px;
   @media (max-width: 1110px) {
      flex-direction: column;
      gap: 50px;
   }
   &__container {
      padding: 0 105px;
      @media (min-width: 1920px) {
         max-width: 1720px;
         margin: 0 auto;
         padding: 0;
      }
      @media (min-width: 1440px), (max-width: 1332px) {
         padding: 0 30px;
      }
      @media (max-width: 480px) {
         padding: 0 10px;
      }
   }
   &__item {
      &:first-child {
         max-width: 704px;
         width: 100%;
         @media (max-width: 1110px) {
            text-align: center;
         }
      }
      &:nth-child(2) {
         position: relative;
         width: 100%;
         @media (max-width: 768px) {
            display: flex;
            flex-wrap: wrap-reverse;
            gap: 20px;
            justify-content: center;
         }
      }
   }
   &__title {
      font-family: "Nunito Sans";
      font-weight: 800;
      font-size: 54px;
      line-height: 1.37;
      color: #252641;
      margin-bottom: 48px;
      @media (max-width: 480px) {
         font-size: 42px;
         margin-bottom: 28px;
      }
      span {
         color: #7f2736;
      }
   }
   &__text {
      font-family: "Nunito Sans";
      font-weight: 400;
      font-size: 24px;
      line-height: 1.6;
      color: #464646;
      max-width: 523px;
      width: 100%;
      margin-bottom: 52px;
      @media (max-width: 1110px) {
         margin: 0 auto 52px;
      }
   }
   &__buttons {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 26px;
      @media (min-width: 1920px) {
         flex-direction: row;
      }
      @media (max-width: 1110px) {
         align-items: center;
         justify-content: center;
      }
   }
   &__btn {
      color: #ffffff;
      padding: 23px 44px;
      background: #b25a59;
      border-radius: 80px;
      margin-bottom: 0;
      max-width: fit-content;
      transition: all 0.3s ease;
      @media (max-width: 400px) {
         width: 100%;
         padding: 17px 0;
         min-width: 100%;
      }
      &:hover {
         transition: all 0.3s ease;
         background: #964b49;
      }
   }
   &__watch {
      display: flex;
      align-items: center;
      gap: 32px;
      margin-bottom: 0;
      transition: all 0.3s ease;
      position: relative;
      max-width: fit-content;
      p {
         z-index: 1;
         padding: 0 20px 0 0;
      }
      @media (max-width: 1110px) {
         justify-content: center;
      }
      @media (max-width: 480px) {
         flex-direction: column;
         gap: 10px;
         text-align: center;
      }
      &::after {
         content: "";
         width: 100%;
         height: 100%;
         background: #ffffff;
         border-radius: 80px;
         transition: all 0.3s ease;
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         transform: translate(-50%, 0) scale(0);
         z-index: 0;
         @media (max-width: 480px) {
            display: none;
         }
      }
      &:hover {
         &::after {
            transition: all 0.3s ease;
            transform: translate(0%, 0) scale(1);
         }
         .hero__play {
            border: 1px solid #252641;
            z-index: 1;
            @media (max-width: 480px) {
               border: none;
            }
         }
         .play {
            transition: all 0.3s ease;
            transform: scale(1.2);
            border-radius: 5px;
            z-index: 1;
         }
      }
   }
   &__play {
      background: #ffffff;
      box-shadow: 2px 20px 60px rgba(61, 155, 185, 0.1);
      width: 80px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      .play {
         transition: all 0.3s ease;
         width: 0;
         height: 0;
         border-radius: 1px;
         border-top: 12px solid transparent;
         border-left: 24px solid #23bdee;
         border-bottom: 12px solid transparent;
         margin-left: 10px;
      }
   }
   &__img {
      width: 100%;
      height: 100%;
      @media (max-width: 768px) {
         flex: 0 1 100%;
      }
   }
   &__content {
      background: rgba(255, 255, 255, 0.8);
      backdrop-filter: blur(20px);
      border-radius: 20px;
      padding: 15px 57px 15px 28px;
      display: flex;
      gap: 32px;
      max-width: fit-content;
      position: absolute;
      @media (max-width: 1110px) {
         left: 50% !important;
         transform: translate(-50%, 0);
      }
      @media (max-width: 768px) {
         position: relative;
         top: 0 !important;
         bottom: 0 !important;
         left: 0 !important;
         transform: translate(0, 0);
         flex: 0 1 60%;
         width: 100% !important;
         padding: 15px 15px 15px 20px;
         max-width: none !important;
         align-items: center;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
      @media (max-width: 400px) {
         justify-content: center;
         flex-direction: column;
         text-align: center;
         gap: 10px;
      }
      &:nth-child(2) {
         left: -15px;
         top: 105px;
         @media (min-width: 1920px) {
            top: 185px;
         }
         @media (max-width: 1332px) {
            top: 30px;
         }
      }
      &:nth-child(3) {
         left: -27px;
         bottom: 38px;
         .hero__content-img {
            background: #f0c53e;
            width: 82px;
            height: 93px;
         }
         .hero__content-text {
            margin-bottom: 19px;
         }
      }
   }
   &__content-img {
      background: #ee5060;
      border-radius: 8px;
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
   }
   &__content-text,
   &__content-title {
      margin-bottom: 0;
   }
   &__content-text {
      font-family: "Nunito Sans";
      font-weight: 600;
      font-size: 20px;
      line-height: 1.8;
      letter-spacing: 0.02em;
      color: #545567;
   }
   &__content-btn {
      padding: 12px 48px;
      color: #ffffff;
      white-space: nowrap;
   }
}
