.section {
   &__accept {
      padding: 0 0 236px;
   }
}
.accept {
   text-align: center;
   &__title {
      font-weight: 600;
      font-size: 44px;
      line-height: 1.8;
      color: #252641;
      margin-bottom: 20px;
      @media (max-width: 480px){
         font-size: 34px;
      }
      @media (max-width: 350px){
         font-size: 27px;
      }
      span {
         color: #7f2736;
      }
   }
   &__text {
      font-weight: 400;
      font-size: 24px;
      line-height: 1.8;
      letter-spacing: 0.02em;
      color: #2c2d32;
      margin-bottom: 80px;
      @media (max-width: 480px){
         margin-bottom: 40px;
         font-size: 18px;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 100px;
      @media (max-width: 992px){
         gap: 20px;
      }
      @media (max-width: 768px){
      flex-direction: column;
      }
   }
   &__item {
      background: url(..//img/accept-img1.png);
      border: 1px solid #000000;
      border-radius: 20px;
      width: 100%;
      height: 400px;
      background-size: contain !important;
      position: relative;
      z-index: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      &:nth-child(2){
         background: url(..//img/accept-img2.png);
      }
      @media (max-width: 400px){
         padding: 10px;
      }
      &::after {
         content: "";
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         width: 100%;
         height: 100%;
         background: rgba(127, 39, 54, 0.8);
         z-index: -1;
         border-radius: 20px;
      }
   }
   &__item-title {
      font-weight: 600;
      font-size: 32px;
      line-height: 1.5;
      color: #ffffff;
      margin-bottom: 17px;
      @media (max-width: 400px){
         font-size: 24px;
      }
   }
   &__item-btn {
      font-weight: 500;
      font-size: 22px;
      line-height: 1.5;
      color: #ffffff;
      padding: 24px 58px;
      border: 1px solid #ffffff;
      border-radius: 80px;
      transition: all .3s ease;
      @media (max-width: 400px){
         width: 100%;
         padding: 17px 0;
      }
      &:hover{
         color: #7f2736;
         background: #ffffff;
         transition: all .3s ease;
      }
   }
}
