.section {
   &__review {
      padding: 0 0 207px;
   }
}
.review {
   display: flex;
   gap: 100px;
   @media (max-width: 1332px){
      gap: 50px;
   }
   @media (max-width: 1110px){
      flex-direction: column;
   }
   &__item {
      min-width: 618px;
      width: 100%;
      @media (max-width: 1332px){
         min-width: 500px;
      }
      @media (max-width: 768px){
         min-width: fit-content;
      }
   }
   &__subtitle {
      font-family: "Nunito Sans";    
      font-weight: 400;
      font-size: 20px;
      line-height: 1.35;
      letter-spacing: 0.2em;
      color: #2c2d32;
      position: relative;
      margin-left: 110px;
      margin-bottom: 32px;
      @media (max-width: 400px){
         margin-left: 60px;
         margin-bottom: 22px;
      }
      &::after {
         content: "";
         width: 80px;
         height: 1px;
         background: #2c2d32;
         position: absolute;
         top: 50%;
         left: -110px;
         transform: translate(0, -50%);
         @media (max-width: 480px){
            width: 40px;
            left: -60px;
         }
      }
   }
   &__title {
      font-family: "Nunito Sans";     
      font-weight: 700;
      font-size: 60px;
      line-height: 1.36;
      color: #7f2736;
      margin-bottom: 29px;
      @media (max-width: 768px){
         font-size: 48px;
      }
      @media (max-width: 480px){
         font-size: 36px;
      }
   }
   &__text {
      font-weight: 400;
      font-size: 26px;
      line-height: 1.6;
      letter-spacing: 0.02em;
      color: #2c2d32;
      margin-bottom: 26px;
      @media (max-width: 480px){
         font-size: 20px;
         margin-bottom: 16px;
         &:nth-child(5){
            margin-bottom: 28px;
         }
      }
      &:nth-child(5) {
         margin-bottom: 48px;
      }
   }
   &__btn {
      display: flex;
      align-items: center;
      font-weight: 400;
      font-size: 22px;
      line-height: 1.6;
      letter-spacing: 0.02em;
      color: #ee5060;
      padding: 23px 133px 23px 58px;
      border: 1px solid #ee5060;
      border-radius: 80px;
      position: relative;
      max-width: fit-content;
      transition: all .3s ease;
      &:hover{
         background: #ee5060;
         color: #ffffff;
         transition: all .3s ease;
         .review__btn-img{
            border: 1px solid #ffffff;
            transition: all .3s ease;
            path{
               fill: #ffffff;
               transition: all .3s ease;
            }
         }
      }
      @media (max-width: 480px){
         padding: 13px 80px 12px 23px;
         margin: 0 auto;
      }
      @media (max-width: 350px){
         white-space: nowrap;
         font-size: 18px;
         padding: 13px 70px 12px 15px;
      }
   }
   &__btn-img {
      width: 80px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #ee5060;
      border-radius: 50%;
      position: absolute;
      right: 0;
      z-index: 99;
      transition: all .3s ease;
      path{
         transition: all .3s ease;
      }
      @media (max-width: 480px){
         width: 60px;
         height: 60px;
      }
      @media (max-width: 350px){
         width: 55px;
         height: 55px;
      }
   }
   &__swiper {
      margin: 0 !important;
      @media (max-width: 1110px){
         max-width: 700px;
         margin: 0 auto !important;
      }
      @media (max-width: 768px){
         max-width: none !important;
         margin: 0 !important;
      }
      .swiper-next {
         width: 80px;
         height: 80px;
         display: flex;
         justify-content: center;
         align-items: center;
         background: #ffffff;
         box-shadow: 0px 4px 60px rgba(41, 44, 124, 0.15);
         position: absolute;
         top: 288px;
         right: 0px;
         z-index: 99;
         border-radius: 50%;
         cursor: pointer;
         @media (max-width: 400px){
            display: none;
         }
      }
      .slide {
         position: relative;
         padding-bottom: 200px;
         @media (max-width: 768px){
            padding-bottom: 0;
         }
         &__img{
            @media (max-width: 768px){
               width: 100%;
               img{
                  width: 100%;
               }
            }
         }
         &__container {
            background: #ffffff;
            box-shadow: 2px 4px 60px rgba(41, 44, 124, 0.1);
            border-radius: 20px;
            position: relative;
            padding: 41px 58px 29px 73px;
            position: absolute;
            left: 50px;
            bottom: 0px;
            @media (max-width: 768px){
               left: 0;
               bottom: 100px;
               width: 100%;
               position: relative;
               padding: 42px 30px 29px;
            }
            &::after {
               content: "";
               position: absolute;
               top: 0;
               bottom: 0;
               left: 0;
               height: 100%;
               width: 14px;
               background: #ee5060;
               box-shadow: 2px 4px 60px rgba(41, 44, 124, 0.1);
               border-radius: 20px 0px 0px 20px;
            }
         }
         &__text {
            font-family: "Nunito Sans";          
            font-weight: 400;
            font-size: 22px;
            line-height: 180%;
            letter-spacing: 0.02em;
            color: #2c2d32;
            margin-bottom: 31px;
            @media (max-width: 400px){
               font-size: 16px;
            }
         }
         &__contact {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width: 400px){
               flex-direction: column;
               justify-content: center;
               gap: 10px;
            }
         }
         &__title {
            font-family: "Nunito Sans";         
            font-weight: 600;
            font-size: 24px;
            line-height: 1.8;
            color: #2c2d32;
         }
         &__rate {
            display: flex;
            align-items: center;
            gap: 4px;
            margin-bottom: 12px;
         }
         &__subtext {
            font-family: "Nunito Sans";           
            font-weight: 600;
            font-size: 18px;
            line-height: 1.8;
            letter-spacing: 0.02em;
            color: #80819a;
            text-align: center;
         }
      }
   }
}
