.section {
   &__team {
      padding: 0 0 155px;
   }
}
.team {
   display: flex;
   align-items: flex-start;
   gap: 126px;
   @media (max-width: 1332px){
      gap: 30px;
   }
   @media (max-width: 1110px){
      flex-direction: column-reverse;
   }
   &__item {
      &:first-child {
         width: 100%;
         img {
            width: 100%;
            height: auto;
         }
      }
      &:nth-child(2){
         max-width: 542px;
         width: 100%;
         margin-top: 100px;
         @media (max-width: 1332px){
            margin-top: 0;
         }
         @media (max-width: 1110px){
            max-width: 100%;
         }
      }
   }
   &__content{
      display: flex;
      gap: 42px;
      margin-bottom: 40px;
      @media (max-width: 400px){
         gap: 22px;
      }
   }
   &__title {
      font-family: "Nunito Sans";      
      font-weight: 700;
      font-size: 40px;
      line-height: 1.6;
      color: #2c2d32;
      margin-bottom: 51px;
      @media (max-width: 480px){
         font-size: 30px;
         margin-bottom: 30px;
      }
      span{
         color: #7f2736;
      }
   }
   &__item-img {
      background: #fbfbfb;
      box-shadow: 0px 15px 44px rgba(13, 15, 28, 0.12);
      max-width: 60px;
      width: 100%;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
   }
   &__item-text {
      font-weight: 400;
      font-size: 22px;
      line-height: 1.8;
      letter-spacing: 0.02em;
      color: #696984;
      @media (max-width: 480px){
         font-size: 18px;
      }
   }
}
