.section {
   &__tour {
      padding: 165px 0 161px;
   }
}
.tour {
   text-align: center;
   &__title {
      font-weight: 700;
      font-size: 36px;
      line-height: 1.8;
      color: #252641;
      margin-bottom: 20px;
      @media (max-width: 480px){
         font-size: 30px;
      }
      @media (max-width: 400px){
         font-size: 24px;
      }
      span {
         color: #7f2736;
      }
   }
   &__text {
      font-family: "Poppins";
      font-weight: 400;
      font-size: 24px;
      line-height: 1.8;
      color: #2c2d32;
      margin-bottom: 64px;
      @media (max-width: 480px){
         font-size: 20px;
      }
      @media (max-width: 400px){
         font-size: 18px;
      }
   }
   &__items {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
   }
   &__item {
      flex: 1 0 33%;
      width: 100%;
      padding: 30px;
      @media (max-width: 992px){
         flex: 0 1 50%;
      }
      @media (max-width: 768px){
         padding: 30px 10px;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
         padding: 20px 0;
      }
   }
   &__img {
      width: 100%;
      height: auto;
      margin-bottom: 32px;
      @media (max-width: 1110px){
         margin-bottom: 20px;
      }
      img {
         box-shadow: 0px 10px 60px rgba(38, 45, 118, 0.08);
         border-radius: 20px;
         width: 100%;
         height: 100%;
      }
   }
   &__iterm-title {
      font-weight: 500;
      font-size: 30px;
      line-height: 1.5;
      color: #7f2736;
   }
}
