.section {
   &__choose {
      padding: 0 0 194px;
   }
}
.choose {
   text-align: center;
   &__title {
      font-family: "Poppins";
      font-weight: 700;
      font-size: 36px;
      line-height: 1.8;
      color: #2c2d32;
      margin-bottom: 11px;
      @media (max-width: 480px){
         font-size: 30px;
      }
      span {
         color: #7f2736;
      }
   }
   &__text {
      font-family: "Poppins";
      font-weight: 400;
      font-size: 24px;
      line-height: 1.8;
      color: #2c2d32;
      max-width: 837px;
      width: 100%;
      margin: 0 auto 117px;
      @media (max-width: 480px){
         font-size: 20px;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 60px;
      @media (max-width: 1110px){
         flex-wrap: wrap;
      }
   }
   &__item {
      background: #ffffff;
      box-shadow: 0px 10px 60px rgba(38, 45, 118, 0.08);
      border-radius: 20px;
      width: 450px;
      height: 430px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      position: relative;
      padding: 0 38px;
      @media (max-width: 1110px){
         flex: 0 1 45%;
         width: 100%;
      }
      @media (max-width: 768px){
         flex: 0 1 100%;
      }
      @media (max-width: 400px){
         padding: 0 10px;
      }
      &:nth-child(2) {
         .choose__item-img {
            background: #f0c53e;
         }
      }
      &:nth-child(3) {
         .choose__item-img {
            background: #af6c2c;
         }
      }
   }
   &__item-img {
      width: 100px;
      height: 100px;
      background: #ee5060;
      box-shadow: 0px 10px 40px rgba(54, 61, 136, 0.06);
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      position: absolute;
      top: -50px;
   }
   &__item-title {
      font-weight: 500;
      font-size: 30px;
      line-height: 1.5;
      color: #7f2736;
      margin-bottom: 69px;
      @media (max-width: 1332px){
         margin-bottom: 29px;
      }
   }
   &__item-text {
      font-weight: 400;
      font-size: 20px;
      line-height: 1.8;
      color: #2c2d32;
   }
}
